import { Component, OnInit } from '@angular/core';
import { Input } from '@angular/core';
import { EventEmitter } from '@angular/core';
import { Output } from '@angular/core';
import { SimpleChanges } from '@angular/core';

@Component({
  selector: 'trill-paginator',
  template: `
  <div class="pagination-container" [hidden]="Hide">
  <div class="pagination-info visible-lg visible-md" style="margin:10px 0 0 0">
    <span>Showing {{itemStart}}-{{itemEnd}} of {{total}} items</span>
  </div>
  <div class="pagination-menus">
    <ul [class]="ulClass" style="padding-left: 20px;">
      <li *ngFor="let item of List" [class]="item.liClass">
        <a title="{{item.title}}" [class]="item.aClass" (click)="item.action()" [innerHtml]="item.value"></a>
      </li>
    </ul>
    <div class="pager-size-wrapper" style="margin:5px 10px">
      <select (ngModelChange)="pageSizeChange($event)" class="pager-size" [(ngModel)]="pageSize">
        <option *ngFor="let c of pageSizeRanges" value="{{c.id}}">{{c.name}}</option>
      </select>
      <span>&nbsp;items per page</span>
    </div>
  </div>
  <div class="pagination-info-center visible-xs visible-sm">
    <span>Showing {{itemStart}}-{{itemEnd}} of {{total}} items</span>
  </div>
</div>`,
  styles: [
    `.pagination {
      margin: 5px 0 !important;
  }
  
  .pagination > .active > a,
  .pagination > .active > span,
  .pagination > .active > a:hover,
  .pagination > .active > span:hover,
  .pagination > .active > a:focus,
  .pagination > .active > span:focus {
      border-color: #337ab7 !important;
      background-color: #337ab7 !important;
      color: white;
  }
  
  .pagination li {
      font-size: 12px !important;
  }
  
  .pagination li:not(.disabled):not(.active) a {
      cursor: pointer;
  }
  
  .pager-size-wrapper {
      display: inline-block;
      vertical-align: top;
  }
  
  .pager-size-wrapper .pager-size {
      color: #788590;
      border: 1px solid #ccc;
      -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
      -webkit-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
      -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
      transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
      height: 30px;
      padding: 5px 10px;
      font-size: 12px;
      border-radius: 3px;
      width: 67px;
  }
  
  .pagination-container {
      width: 100%;
      padding: 10px 10px;
      text-align: center;
  }
  
  .pagination-menus {
      width: 100%;
      margin: 0 auto;
      padding: 0;
  }
  
  .pagination-info {
      float: right;
  }
  `
  ]
})
export class TrillPaginatorComponent implements OnInit {

  @Input() page = 0;
  @Input() pageSize = 1;
  @Input() total = 0;
  @Input() itemEnd: number;
  @Input() itemStart: number;
  @Input() pagesCount: number;
  @Input() pageSizeOptions: Array<number>;
  @Input() disabled = false;
  @Input() dots = '...';
  @Input() ulClass = 'pagination text-bold';
  @Input() activeClass = 'active';
  @Input() disabledClass = 'disabled';
  @Input() adjacent = 2;
  @Output() pagingAction = new EventEmitter<object>();
  @Input() textFirst = '<<';
  @Input() textLast = '>>';
  @Input() textNext = '>';
  @Input() textPrev = '<';
  @Input() textFirstClass = '';
  @Input() textLastClass = '';
  @Input() textNextClass = '';
  @Input() textPrevClass = '';
  @Input() textTitlePage = 'Page {page} of {pagesCount}';
  @Input() textTitleFirst = 'Goto First Page';
  @Input() textTitleLast = 'Goto Last Page';
  @Input() textTitleNext = 'Next Page';
  @Input() textTitlePrev = 'Previous Page';

  @Input() hideIfEmpty = false;
  @Input() showPrevNext = true;
  @Input() showFirstLast = true;


  /**
    * The regex expression to use for any replace methods
    * Feel free to tweak / fork values for your application
    */
  regex = /\{page\}/g;
  regex2 = /\{pagesCount\}/g;

  List = [];
  Hide = false;

  differ: any;

  constructor() { }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['page'] || changes['pageSize'] || changes['total']) {
      console.log('Change detected' + changes);
      this.build();
    }
  }

  /**
     * The main build function used to determine the paging logic
     * Feel free to tweak / fork values for your application
     *
     */
  build() {
    this.List = [];
    // Block divide by 0 and empty page size
    if (!this.pageSize || this.pageSize <= 0) {
      this.pageSize = 1;
    }

    // Determine the last page or total page count
    const pageCount = Math.ceil(this.total / this.pageSize);

    // Validate the scope values to protect against strange states
    this.validateScopeValues(pageCount);

    // Create the beginning and end page values
    var start, finish;

    // Calculate the full adjacency value
    const fullAdjacentSize = (this.adjacent * 2) + 2;

    // Add the Next and Previous buttons to our list
    this.addPrevNext(pageCount, 'prev');

    // If the page count is less than the full adjacnet size
    // Then we simply display all the pages, Otherwise we calculate the proper paging display
    if (pageCount <= (fullAdjacentSize + 2)) {

      start = 1;
      this.addRange(start, pageCount);

    } else {
      // Determine if we are showing the beginning of the paging list
      // We know it is the beginning if the page - adjacent is <= 2
      if (this.page - this.adjacent <= 2) {

        start = 1;
        finish = 1 + fullAdjacentSize;

        this.addRange(start, finish);
        this.addLast(pageCount, finish);
      }
      // Determine if we are showing the middle of the paging list
      // We know we are either in the middle or at the end since the beginning is ruled out above
      // So we simply check if we are not at the end
      // Again 2 is hard coded as we always display two pages after the dots
      // tslint:disable-next-line:one-line
      else if (this.page < pageCount - (this.adjacent + 2)) {

        start = this.page - this.adjacent;
        finish = this.page + this.adjacent;

        this.addFirst(start);
        this.addRange(start, finish);
        this.addLast(pageCount, finish);
      }

      // If nothing else we conclude we are at the end of the paging list
      // We know this since we have already ruled out the beginning and middle above
      // tslint:disable-next-line:one-line
      else {

        start = pageCount - fullAdjacentSize;
        finish = pageCount;

        this.addFirst(start);
        this.addRange(start, finish);
      }
    }

    // Add the next and last buttons to our paging list
    this.addPrevNext(pageCount, 'next');
  }

  /**
   * Validate and clean up any scope values
   * This happens after we have set the scope values
   *
   * @param {int} pageCount - The last page number or total page count
   */
  validateScopeValues(pageCount) {

    // Block where the page is larger than the pageCount
    if (this.page > pageCount) {
      this.page = pageCount;
    }

    // Block where the page is less than 0
    if (this.page <= 0) {
      this.page = 1;
    }

    // Block where adjacent value is 0 or below
    if (this.adjacent <= 0) {
      this.adjacent = 2;
    }

    // Hide from page if we have 1 or less pages
    // if directed to hide empty
    if (pageCount < 1) {
      this.Hide = this.hideIfEmpty;
    }
  }

  /**
     * Assign the method action to take when a page is clicked
     *
     * @param {Object} scope - The local directive scope object
     * @param {int} page - The current page of interest
     */
  internalAction(page, pageSizeChanged) {
    // Block clicks we try to load the active page
    if (!pageSizeChanged && this.page === page) {
      return;
    }

    // Block if we are forcing disabled
    if (this.disabled) {
      return;
    }

    // Update the page in scope
    this.page = page;

    // Pass our parameters to the paging action
    this.pagingAction.emit({
      page: this.page,
      pageSize: this.pageSize,
      total: this.total
    });
  }

  /**
   * Add the first, previous, next, and last buttons if desired
   * The logic is defined by the mode of interest
   * This method will simply return if the scope.showPrevNext is false
   * This method will simply return if there are no pages to display
   *
   * @param {int} pageCount - The last page number or total page count
   * @param {string} mode - The mode of interest either prev or last
   */
  addPrevNext(pageCount, mode) {

    // Ignore if we are not showing
    // or there are no pages to display
    if ((!this.showPrevNext && !this.showFirstLast) || pageCount < 1) {
      return;
    }

    // Local variables to help determine logic
    var disabled, alpha, beta;

    // Determine logic based on the mode of interest
    // Calculate the previous / next page and if the click actions are allowed
    if (mode === 'prev') {

      disabled = this.page - 1 <= 0;
      const prevPage = this.page - 1 <= 0 ? 1 : this.page - 1;

      if (this.showFirstLast) {
        alpha = {
          value: this.textFirst,
          title: this.textTitleFirst,
          aClass: this.textFirstClass,
          page: 1
        };
      }
      if (this.showPrevNext) {
        beta = {
          value: this.textPrev,
          title: this.textTitlePrev,
          aClass: this.textPrevClass,
          page: prevPage
        };
      }
    } else {
      disabled = this.page + 1 > pageCount;
      const nextPage = this.page + 1 >= pageCount ? pageCount : this.page + 1;
      if (this.showPrevNext) {
        alpha = {
          value: this.textNext,
          title: this.textTitleNext,
          aClass: this.textNextClass,
          page: nextPage
        };
      }
      if (this.showFirstLast) {
        beta = {
          value: this.textLast,
          title: this.textTitleLast,
          aClass: this.textLastClass,
          page: pageCount
        };
      }
    }
    // Add alpha items
    if (alpha) {
      // tslint:disable-next-line:prefer-const
      var alphaItem = this.buildItem(alpha, disabled);
      this.List.push(alphaItem);
    }
    // Add beta items
    if (beta) {
      // tslint:disable-next-line:prefer-const
      var betaItem = this.buildItem(beta, disabled);
      this.List.push(betaItem);
    }
  }

  /**
   * Add the first or beginning items in our paging list
   * We leverage the 'next' parameter to determine if the dots are required
   * @param {int} next - the next page number in the paging sequence
   */
  addFirst(next) {

    this.addRange(1, 2);

    // We ignore dots if the next value is 3
    // ie: 1 2 [...] 3 4 5 becomes just 1 2 3 4 5
    if (next !== 3) {
      this.addDots();
    }
  }

  /**
   * Add the last or end items in our paging list
   * We leverage the 'prev' parameter to determine if the dots are required
   *
   * @param {int} pageCount - The last page number or total page count
   * @param {int} prev - the previous page number in the paging sequence
   */
  addLast(pageCount, prev) {

    // We ignore dots if the previous value is one less than our start range
    // ie: 1 2 3 4 [...] 5 6  becomes just 1 2 3 4 5 6
    if (prev !== pageCount - 2) {
      this.addDots();
    }

    this.addRange(pageCount - 1, pageCount);
  }

  /**
       * Adds a range of numbers to our list
       * The range is dependent on the start and finish parameters
       *
       * @param {int} start - The start of the range to add to the paging list
       * @param {int} finish - The end of the range to add to the paging list
       */
  addRange(start, finish) {
    // Add our items where i is the page number
    for (let i = start; i <= finish; i++) {
      var liClass = this.page === i ? this.activeClass : '';

      // Handle items that are affected by disabled
      if (this.disabled) {
        liClass = this.disabledClass;
      }

      const _this = this;
      this.List.push({
        value: i.toString(),
        title: this.textTitlePage.replace(this.regex, i.toString()).replace(this.regex2, this.pagesCount.toString()),
        liClass: liClass,
        action: function () {
          _this.internalAction(i, false);
        },
        data: i
      });
    }
  }
  /**
   * Add Dots ie: 1 2 [...] 10 11 12 [...] 56 57
   * This is my favorite function not going to lie
   */
  addDots() {
    this.List.push({
      value: this.dots,
      liClass: this.disabledClass
    });
  }
  // Create the Add Item Function
  buildItem(item, disabled) {
    const _this = this;
    return {
      title: item.title,
      aClass: item.aClass,
      value: item.aClass ? '' : item.value as string,
      liClass: disabled ? this.disabledClass : '',
      action: function () {
        if (!disabled) {
          _this.internalAction(item.page, false);
        }
      }
    };
  }
  ngOnInit() {
  }
  pageSizeChange(evt) {
    this.pageSize = +evt;
    this.internalAction(1, true);
  }
}
